@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  width: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ul, li, input, textarea {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Gotham Pro", Arial, Verdana, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #40474E;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

body._lock {
  overflow: hidden;
}

.button_news {
  background: #CFDAE3;
  color: #40474E;
  line-height: 130%;
}

.button_article {
  background: #D9D4D4;
  color: #40474E;
  line-height: 120%;
}

.insta__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(207, 218, 227, 0.2);
  justify-content: center;
}
.insta__body {
  max-width: 880px;
  padding: 120px 0;
}
.insta__title {
  position: relative;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 80px;
  font-size: 20px;
  letter-spacing: 2px;
}
.insta__title::before {
  position: absolute;
  content: "";
  width: 200px;
  height: 1px;
  background: #8D9AA5;
  top: 8px;
  left: 0;
}
@media (max-width: 1450px) {
  .insta__title::before {
    display: none;
  }
}
.insta__title::after {
  position: absolute;
  content: "";
  width: 200px;
  height: 1px;
  background: #8D9AA5;
  top: 8px;
  right: 0;
}
@media (max-width: 1450px) {
  .insta__title::after {
    display: none;
  }
}

.ourwork-instance {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
  gap: 20px;
}
.ourwork-instance .item-ourwork {
  height: 280px;
  width: 280px;
  background: #FFFFFF;
  text-align: center;
}
.ourwork-instance .item-ourwork__img {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: #40474E;
  height: 50px;
  width: 50px;
  margin-top: 70px;
  margin-bottom: 15px;
  border-radius: 50%;
}
.ourwork-instance .item-ourwork__title {
  font-size: 24px;
  line-height: 1.2;
  margin-bottom: 18px;
}
.ourwork-instance .item-ourwork__button {
  padding: 14px 30px;
  background-color: #6B97BB;
  color: #F9F9FA;
}