@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  width: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ul, li, input, textarea {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Gotham Pro", Arial, Verdana, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #40474E;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

body._lock {
  overflow: hidden;
}

.button_news {
  background: #CFDAE3;
  color: #40474E;
  line-height: 130%;
}

.button_article {
  background: #D9D4D4;
  color: #40474E;
  line-height: 120%;
}

.our-projects {
  position: relative;
  padding: 110px 0 105px 0;
  overflow: hidden;
}
@media (max-width: 1450px) {
  .our-projects {
    flex-direction: column;
    margin-bottom: 40px;
  }
}
.our-projects__title {
  position: relative;
  max-width: 1470px;
  font-size: 20px;
  padding: 0px 15px 80px 15px;
  margin: 0 auto;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.our-projects__title::after {
  position: absolute;
  content: "";
  top: 8px;
  left: 240px;
  width: 200px;
  height: 1px;
  background: #8D9AA5;
}
@media (max-width: 1450px) {
  .our-projects__title::after {
    width: 150px;
  }
}
.our-projects__container {
  display: flex;
  justify-content: space-between;
  max-width: 1470px;
  margin: 0 auto;
}
@media (max-width: 1080px) {
  .our-projects__container {
    flex-direction: column;
  }
}
.our-projects__img {
  width: 100%;
  height: 100%;
  position: relative;
  text-align: end;
}
.our-projects__img::before {
  content: "";
  position: absolute;
  width: 120px;
  height: 120px;
  z-index: 5;
  top: 59%;
  left: 18%;
  background: url(.././img/ourprojects/magnifying_glass_plus.svg) no-repeat center;
  background-color: rgba(249, 249, 250, 0.2);
}
.our-projects__img img {
  height: 806px;
  max-width: 100%;
  object-fit: cover;
}
@media (max-width: 1450px) {
  .our-projects__img img {
    height: 100%;
  }
}
.our-projects__button-prev, .our-projects__button-next {
  display: flex;
  box-sizing: border-box;
  text-align: center;
  justify-content: center;
  align-items: center;
  width: 54px;
  height: 54px;
  background-position: center;
  border: 1px solid #98A4AE;
  fill: grey;
}
.our-projects__button-prev > svg, .our-projects__button-next > svg {
  fill: #40474E;
}
.our-projects__button-prev > svg:hover, .our-projects__button-next > svg:hover {
  cursor: pointer;
  fill: #AA182C;
}
.our-projects__content {
  flex: 1 1 auto;
}

.content__container {
  padding: 0 15px 0 85px;
}
.content .block-button {
  display: flex;
  justify-content: space-between;
  margin-bottom: 85px;
  gap: 30px;
}
@media (max-width: 1450px) {
  .content .block-button {
    flex-wrap: wrap;
    margin-bottom: 40px;
  }
}
.content .block-button__link {
  text-transform: uppercase;
  font-size: 14px;
  color: #6B97BB;
  border: 1px solid #6B97BB;
  white-space: nowrap;
  display: flex;
  align-items: center;
  padding: 0 40px;
}
.content__title {
  font-size: 43px;
  line-height: 1.3;
  padding: 0 0 35px 0;
}
@media (max-width: 1450px) {
  .content__title {
    font-size: 26px;
  }
}
.content__description {
  width: 517px;
  font-weight: 300;
  font-size: 18px;
  line-height: 1.6;
  color: #40474E;
  letter-spacing: 1.2px;
}
@media (max-width: 1450px) {
  .content__description {
    max-width: 100%;
    margin-bottom: 25px;
  }
}
.content__description p {
  padding: 0 0 10px 0;
}

.block-button__slider-buttons {
  display: flex;
  gap: 15px;
  align-items: center;
  justify-content: space-between;
}

.slider-our-projects.swiper-container {
  position: absolute;
  top: 62%;
  width: 100%;
  z-index: 5;
  padding-left: 50%;
  overflow: hidden;
}
@media (max-width: 1450px) {
  .slider-our-projects.swiper-container {
    position: relative;
    padding-left: 0;
  }
}
.slider-our-projects__slide.swiper-slide {
  max-width: 327px;
}
.slider-our-projects__slide.swiper-slide img {
  max-width: 100%;
  object-fit: contain;
}