@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  width: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ul, li, input, textarea {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Gotham Pro", Arial, Verdana, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #40474E;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

body._lock {
  overflow: hidden;
}

.button_news {
  background: #CFDAE3;
  color: #40474E;
  line-height: 130%;
}

.button_article {
  background: #D9D4D4;
  color: #40474E;
  line-height: 120%;
}

.questions {
  background-color: rgba(229, 229, 229, 0.2);
  padding: 120px 0 120px 0;
}
.questions__wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  max-width: 1470px;
  margin: 0 auto;
  height: 893px;
  background: url(".././img/questions/PALMIRA_decor2_4000x4000.png") no-repeat;
  background-position: 15px 0;
}
@media (max-width: 1450px) {
  .questions__wrapper {
    background-position: 0;
  }
}
.questions__form {
  margin: 0 65px 0 0;
}

.form {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: baseline;
  max-width: 735px;
  width: 100%;
  padding: 75px 155px 105px 112px;
  background-color: #FFFFFF;
}
@media (max-width: 1450px) {
  .form {
    width: 90%;
    margin: 0 auto;
    padding: 15px;
  }
}
.form::before {
  content: "";
  position: absolute;
  display: block;
  width: 690px;
  height: 685px;
  top: 2%;
  left: 4%;
  z-index: 0;
  border: 1px solid #98A4AE;
  transform: matrix(-0.26, 0.97, -0.97, -0.26, 0, 0);
}
@media (max-width: 1450px) {
  .form::before {
    display: none;
  }
}
.form__head {
  font-size: 44px;
  margin: 0 0 18px 0;
}
.form__text {
  max-width: 420px;
  font-size: 18px;
  margin: 0 0 40px 0;
}
.form__questionary {
  display: flex;
  flex-direction: column;
  max-width: 470px;
  width: 100%;
}
.form__input {
  padding: 15px 20px;
  margin: 0 0 17px 0;
  z-index: 5;
  border: 1px solid #98A4AE;
}
.form__input::placeholder {
  font-family: "Gotham Pro";
  font-weight: 300;
  font-size: 16px;
  line-height: 1.2;
  color: #98A4AE;
}
.form textarea {
  height: 100px;
  margin: 0 0 25px 0;
  resize: none;
}
.form__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  z-index: 5;
  padding: 19px 30px 19px 30px;
  background-color: #6B97BB;
  border: none;
  cursor: pointer;
}
.form__button > span {
  font-family: "Gotham Pro";
  color: #FFFFFF;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}