@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  width: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ul, li, input, textarea {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Gotham Pro", Arial, Verdana, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #40474E;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

body._lock {
  overflow: hidden;
}

.button_news {
  background: #CFDAE3;
  color: #40474E;
  line-height: 130%;
}

.button_article {
  background: #D9D4D4;
  color: #40474E;
  line-height: 120%;
}

.article {
  background-color: rgba(229, 229, 229, 0.2);
  padding-bottom: 100px;
}
.article__container {
  display: flex;
  justify-content: space-between;
  padding: 120px 0 0 0;
}
.article__body {
  display: flex;
  flex-direction: column;
  overflow-x: visible;
  padding: 120px 0;
}
.article__caption {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  margin-bottom: 55px;
}
@media (max-width: 1450px) {
  .article__caption {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    row-gap: 30px;
  }
}
.article__title {
  position: relative;
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: 2px;
}
.article__title::after {
  position: absolute;
  background: #8D9AA5;
  content: "";
  width: 200px;
  height: 1px;
  top: 8px;
  left: 107%;
  letter-spacing: 5px;
}
@media (max-width: 1450px) {
  .article__title::after {
    display: none;
  }
}
.article__button {
  color: #6B97BB;
  padding: 18px 55px;
  text-transform: uppercase;
  border-width: 1px;
  border-style: solid;
  cursor: pointer;
}

.slider-article.swiper-container {
  overflow: hidden;
  max-width: 100%;
  height: 526px;
  min-width: 380px;
}
.slider-article .swiper-slide {
  min-width: 380px;
  justify-content: center;
  height: 526px;
  display: flex;
  align-items: center;
}
.slider-article__slide-wrapper {
  height: 486px;
  overflow: visible;
  box-sizing: border-box;
  border: 1px solid #CFDAE3;
}
@media (min-width: 1450px) {
  .slider-article__slide-wrapper:hover {
    outline: 1px solid #CFDAE3;
    outline-offset: 10px;
  }
}
.slider-article__image {
  max-width: 100%;
  height: 325px;
}
.slider-article__content {
  padding: 20px;
}
.slider-article__statistic {
  display: flex;
  margin-bottom: 15px;
}
.slider-article__statistic > span {
  padding-left: 24px;
  margin-right: 15px;
  display: inline-block;
  color: #40474E;
  font-size: 12px;
  line-height: 1.2;
}
.slider-article__view {
  background: url(".././img/articles/eye.png") no-repeat;
  background-position: left;
}
.slider-article__message {
  background: url(".././img/articles/message.png") no-repeat;
  background-position: left;
}
.slider-article__link {
  max-width: 340px;
  margin-bottom: 16px;
  overflow: hidden;
  display: inline-block;
  font-size: 20px;
  line-height: 1.2;
  cursor: pointer;
  text-overflow: ellipsis;
  display: -webkit-box;
}
.slider-article__link:hover {
  color: #6B97BB;
}

.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.info__category {
  padding: 4px 14px;
  text-transform: uppercase;
}
.info__date {
  font-size: 14px;
  line-height: 1.3;
  text-transform: uppercase;
}