@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  width: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ul, li, input, textarea {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Gotham Pro", Arial, Verdana, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #40474E;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

body._lock {
  overflow: hidden;
}

.button_news {
  background: #CFDAE3;
  color: #40474E;
  line-height: 130%;
}

.button_article {
  background: #D9D4D4;
  color: #40474E;
  line-height: 120%;
}

.footer {
  display: flex;
  flex-direction: column;
  background-color: #6A737A;
}
.footer__wrapper {
  display: grid;
  padding: 80px 10px 78px 10px;
  grid-template-columns: 0.78fr 2.7fr 1.1fr;
}
@media (max-width: 1450px) {
  .footer__wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
.footer__additional {
  display: flex;
  justify-content: space-between;
  padding: 0 0 20px 0;
}

.container-footer {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 1470px;
}

.social__logo {
  margin-bottom: 40px;
}
.social__links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 132px;
  height: 24px;
}

.footer-menu {
  display: grid;
  font-family: "Gotham Pro";
  font-weight: 400;
  color: #FFFFFF;
  grid-template-columns: 0.95fr 0.95fr 1.2fr 0.99fr;
}
@media (max-width: 1450px) {
  .footer-menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 15px;
  }
}
.footer-menu__gap {
  display: grid;
  row-gap: 25px;
  grid-template-rows: 0.8fr 0.56fr;
}
@media (max-width: 1450px) {
  .footer-menu__gap {
    margin: 0 15px;
  }
}
.footer-menu__header {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 400;
  color: #FFFFFF;
  margin: 0 0 13px 0;
}
.footer-menu__item {
  margin: 0 0 5px 0;
}
.footer-menu__link {
  display: inline-block;
  border-bottom: 1px solid transparent;
  color: #FFFFFF;
  font-size: 14px;
  line-height: 1.2;
  padding: 0 0 2px 0;
}
.footer-menu__link:hover {
  border-color: #FFFFFF;
}

.review__button {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 262px;
  padding: 18px;
  cursor: pointer;
  margin: 0 0 15px 85px;
}
@media (max-width: 1450px) {
  .review__button {
    margin: 10px;
  }
}
.review__button > span {
  display: inline-block;
  color: #FFFFFF;
  font-family: "Gotham Pro";
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.05em;
  margin: 0 30px 0 0;
  text-transform: uppercase;
  white-space: nowrap;
}
.review__button {
  background-color: #6B97BB;
  border: 1px solid #6B97BB;
}
.review__button-outline {
  padding: 20px;
  border: 1px solid #FFFFFF;
  background-color: transparent;
}
.review__button-outline > span {
  margin: 0 25px 0 0;
}

.copyright {
  display: grid;
  grid-template-columns: 1.75fr 1fr 1fr;
  padding: 0 15px;
}
.copyright__year {
  font-family: "Gotham Pro";
  font-weight: normal;
  font-size: 14px;
  line-height: 1.2;
  margin-top: -2px;
  color: #FFFFFF;
}
.copyright__language {
  display: flex;
  align-items: center;
  margin-top: -2px;
}
.copyright__element:not(:last-child) {
  margin-right: 8px;
}
.copyright__separator {
  background-color: #FFFFFF;
  width: 1px;
  height: 10px;
  opacity: 0.4;
}
.copyright__separator-highlight {
  opacity: 1;
}
.copyright__lang-highlight {
  opacity: 1;
  color: white;
}
.copyright__lang {
  color: #FFFFFF;
  font-family: "Gotham Pro";
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  opacity: 0.4;
}
.copyright__development {
  display: flex;
  align-items: center;
  margin-left: auto;
  color: #FFFFFF;
  font-family: "Gotham Pro";
  font-size: 12px;
  line-height: 1.2;
  font-weight: normal;
}
.copyright__development > a {
  margin: 0 0 0 5px;
}