@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  width: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ul, li, input, textarea {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Gotham Pro", Arial, Verdana, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #40474E;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

body._lock {
  overflow: hidden;
}

.button_news {
  background: #CFDAE3;
  color: #40474E;
  line-height: 130%;
}

.button_article {
  background: #D9D4D4;
  color: #40474E;
  line-height: 120%;
}

.intro {
  position: relative;
  overflow: hidden;
}
.intro__wrapper {
  position: absolute;
  max-width: 1470px;
  margin: 0 auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.introSwiper__item {
  position: relative;
}
.introSwiper__img img {
  width: 100%;
  object-fit: cover;
}
.introSwiper__img::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(64, 71, 78, 0.45);
}
.introSwiper__introduction {
  position: absolute;
  top: 430px;
  padding: 0 0 0 15px;
}
@media screen and (max-width: 840px) {
  .introSwiper__introduction {
    width: 80%;
  }
}

.introduction__title {
  font-size: 86px;
  line-height: 1.16;
  color: #FFFFFF;
  padding: 0 0 10px 0;
  text-transform: uppercase;
}
@media screen and (max-width: 1450px) {
  .introduction__title {
    font-size: 30px;
  }
}
.introduction__description {
  max-width: 540px;
  color: #FFFFFF;
  font-size: 30px;
}
@media screen and (max-width: 1450px) {
  .introduction__description {
    font-size: 30px;
  }
}

.intro-navigation {
  position: relative;
  z-index: 1;
}

.intro-nav {
  display: flex;
  position: absolute;
  min-width: 100%;
  bottom: 60px;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}
.intro-nav__wheel {
  padding: 0 0 0 68px;
}
.intro-nav__arrows {
  display: flex;
  width: 100%;
  max-width: 142px;
  margin: -2px 0 0 0;
  justify-content: space-between;
  align-items: center;
}
.intro-nav__arrows > svg:hover {
  cursor: pointer;
  fill: #AA182C;
}
.intro-nav__separator {
  height: 35px;
  width: 1.5px;
  background-color: #FFFFFF;
}

.intro-social {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 362px;
  right: 15px;
  z-index: 1;
}
.intro-social__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 32px;
  width: 32px;
  margin: 0 0 20px 0;
}

.swiper-pagination-horizontal {
  display: flex;
  align-items: center;
  padding: 0 0 2px 0;
}

.swiper-horizontal .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  width: auto;
}

.swiper-horizontal .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 17px 0 0;
}

.swiper-pagination-bullet {
  display: inline-block;
  height: 5px;
  width: 5px;
  border-radius: 50%;
  background-color: #FFFFFF;
  margin: 0;
  cursor: pointer;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  height: 15px;
  width: 15px;
  border: 1px solid #FFFFFF;
  background-color: transparent;
}