@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Light.woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro.woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Medium.woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro";
  src: url("../fonts/GothamPro/GothamPro-Bold.woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
  width: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, ul, li, input, textarea {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: "Gotham Pro", Arial, Verdana, sans-serif;
  font-weight: normal;
  font-size: 16px;
  color: #40474E;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  display: flex;
  max-width: 1470px;
  margin: 0 auto;
  padding: 0 15px;
}

body._lock {
  overflow: hidden;
}

.button_news {
  background: #CFDAE3;
  color: #40474E;
  line-height: 130%;
}

.button_article {
  background: #D9D4D4;
  color: #40474E;
  line-height: 120%;
}

.header {
  background: #E5E5E5;
  z-index: 50;
}
.header__container {
  background: #E5E5E5;
}
@media (max-width: 1450px) {
  .header__logo img {
    width: 56px;
    height: 50px;
  }
}
.header__body {
  display: flex;
  flex-grow: 1;
  align-items: center;
}
.header__logo {
  cursor: pointer;
  padding: 0 77px 0 0;
  z-index: 45;
  width: 100%;
}
.header__right-part {
  display: flex;
  align-items: center;
  margin-right: -15px;
  z-index: 45;
}
@media (max-width: 1450px) {
  .header__right-part {
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.menu__body {
  display: flex;
  align-items: center;
}
@media (max-width: 1450px) {
  .menu__body {
    position: fixed;
    height: 100%;
    width: 50%;
    top: 0px;
    left: -100%;
    z-index: 99;
    background-color: #E5E5E5;
    flex-direction: column;
    justify-content: flex-start;
  }
}
.menu__body._show {
  width: 50%;
  top: 0;
  left: 15px;
  padding-top: 100px;
  z-index: 40;
}
.menu__container {
  display: flex;
}
.menu__to-shop {
  border-color: #AA182C;
  white-space: nowrap;
  text-decoration: none;
}
@media (max-width: 1450px) {
  .menu__to-shop {
    border: none;
    padding: 10px 20px 10px 20px;
    border: #AA182C solid 1px;
  }
}
.menu__to-shop:hover {
  color: #AA182C;
}
.menu__list {
  display: flex;
  list-style: none;
  padding-left: 65px;
  align-items: center;
  min-width: 830px;
}
@media (max-width: 1450px) {
  .menu__list {
    flex-direction: column;
    row-gap: 20px;
  }
}
@media (max-width: 1470px) {
  .menu__list {
    padding: 14px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }
}
.menu__item {
  text-transform: uppercase;
  padding: 0 18px;
}
.menu__link span:hover {
  color: #AA182C;
}
.menu__link span:first-child {
  color: #AA182C;
}
.menu__link:hover {
  color: #AA182C;
}

.button {
  display: inline-block;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.16;
  text-transform: uppercase;
  border-width: 1px;
  border-style: solid;
  cursor: pointer;
  padding: 10px 30px;
}

.lang {
  position: relative;
  z-index: 8;
  background: #E5E5E5;
  position: relative;
  font-size: 16px;
  line-height: 1.16;
  padding-right: 52px;
}
@media (max-width: 1450px) {
  .lang {
    padding: 5px;
  }
}
.lang__current:hover, .lang__item:hover {
  color: #AA182C;
}
.lang.show .lang__body {
  display: block;
  position: absolute;
  top: 30px;
  left: 0;
}
.lang__icon {
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 6px solid #40474E;
}
.lang__header {
  display: flex;
  gap: 8px;
  align-items: center;
  cursor: pointer;
}
.lang__body {
  display: none;
}
.lang__item {
  cursor: pointer;
  list-style: none;
}

.btn-menu {
  display: none;
}
@media (max-width: 1450px) {
  .btn-menu {
    display: block;
    position: relative;
    flex: 0 0 30px;
    height: 20px;
    cursor: pointer;
    border: none;
    z-index: 99;
    background-color: #E5E5E5;
    margin-right: 30px;
  }
  .btn-menu span, .btn-menu::before, .btn-menu::after {
    content: "";
    transition: all 0.3s ease 0s;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #40474E;
  }
  .btn-menu::before {
    top: 0px;
  }
  .btn-menu::after {
    bottom: 0px;
  }
  .btn-menu span {
    top: calc(50% - 1px);
  }
  .btn-menu.menu-open span {
    transform: scale(0);
  }
  .btn-menu.menu-open::before {
    transform: rotate(-45deg);
    top: calc(50% - 1px);
  }
  .btn-menu.menu-open::after {
    transform: rotate(45deg);
    top: calc(50% - 1px);
  }
}

.icons {
  align-items: center;
  width: 174px;
  height: 24px;
  margin-top: 3px;
}
.icons__container {
  display: flex;
  flex-wrap: nowrap;
}
@media (max-width: 480px) {
  .icons__container {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.icons__item {
  width: 22px;
}
.icons__link {
  padding: 14px;
}
.icons__link svg {
  fill: #40474E;
}
.icons__link svg:hover {
  fill: #AA182C;
}

.heart {
  position: relative;
}
.heart::after {
  content: "";
  position: absolute;
  top: 7px;
  left: 30px;
  width: 15px;
  height: 15px;
  background-color: #AA182C;
  transform: rotate(15deg);
}
.heart__likes {
  position: absolute;
  font-family: "Gotham Pro";
  font-weight: 700;
  font-size: 8px;
  color: #FFFFFF;
  display: block;
  top: 11px;
  left: 33px;
  z-index: 5;
}